@import './../../styles/colors.scss';
$btnSize: 40px;
$qrImgSize: 200px;
$tabHeight: 40px;



.page-home {
  // background-color: $colorBackground;

  .tab-products {

    margin: 0 auto;
    max-width: 100%;
    white-space: nowrap;
    min-height: 50px;
    overflow-x: scroll;
    -webkit-justify-content: flex-start;
    justify-content: flex-start;
    background-color: #80808024;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    text-align: center;

    .tab-item {
      min-width: 300px;
      max-width: 300px;

      text-align: center;
      border-right: 1px solid black;
      cursor: pointer;
      height: 50px;
      display: inline-block;
      vertical-align: middle;



      p {
        font-weight: 700;
        text-transform: capitalize;
        margin: 0;
        color: black;
        display: inline-block;
        vertical-align: middle;
        font-size: 18px;
        overflow: hidden;

        white-space: nowrap;
        text-overflow: ellipsis;
        width: 50%;
      }

      &.active {
        background: rgba(20, 188, 91, 1);
        background: -moz-linear-gradient(top, rgba(20, 188, 91, 1) 0%, rgba(210, 255, 82, 1) 100%);
        background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(20, 188, 91, 1)), color-stop(100%, rgba(210, 255, 82, 1)));
        background: -webkit-linear-gradient(top, rgba(20, 188, 91, 1) 0%, rgba(210, 255, 82, 1) 100%);
        background: -o-linear-gradient(top, rgba(20, 188, 91, 1) 0%, rgba(210, 255, 82, 1) 100%);
        background: -ms-linear-gradient(top, rgba(20, 188, 91, 1) 0%, rgba(210, 255, 82, 1) 100%);
        background: linear-gradient(to bottom, rgba(20, 188, 91, 1) 0%, rgba(210, 255, 82, 1) 100%);
      }

      &:last-child {
        border-right: none;
      }

      &:before {
        content: '';
        vertical-align: middle;
        display: inline-block;
        height: 100%;
        ;
      }
    }
  }

  .rate-product {
    margin: 20px auto;
    max-width: 80%;
    display: flex;
    min-height: 180px;
    justify-content: space-around;

    .rate-item {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      border-radius: 14px;
      -webkit-border-radius: 14px;
      -moz-border-radius: 14px;
      -ms-border-radius: 14px;
      -o-border-radius: 14px;
      text-align: center;
      background-color: #80808024;
      width: calc((100% / 5) - 5px);
      background: #f6f6f6;
      background: linear-gradient(to bottom, #eaeaea 25%, #f7f7f7 44%, #f6f6f6 47%, #ededed 55%);
      cursor: pointer;

      p {
        font-size: 18px;

        @media only screen and (max-width: 370px) {
          font-size: 13px;


        }
      }

      .rate-icon {
        width: 50px;

        @media only screen and (max-width: 600px) {
          width: 30px;

        }
      }

      * {
        transition: all 0.3s ease;
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        -ms-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
      }

      &:hover {
        img {
          width: 60px;

          @media only screen and (max-width: 600px) {
            width: 40px;

          }
        }

        p {
          font-size: 11px;

        }
      }

      &.active {
        img {
          width: 90px;

          @media only screen and (max-width: 600px) {
            width: 50px;

          }
        }

        p {
          font-size: 11px;

        }
      }


    }
  }

  .comments {
    max-width: 80%;
    margin: 0 auto;

    .comment-input {
      border: 2px solid black;
      border-radius: 10px;
      // max-width: 80%;
      width: calc(100% - 79px);
      padding: 5px 10px;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
    }
  }

  .submit {
    cursor: pointer;
    background-color: $colorGreen;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    display: block;
    margin: 15px auto;
    border: none;
    padding: 12px;
    min-width: 150px;
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-x: 6px;
    -o-border-radius: 6px;



    &.disabled {
      opacity: 0.6;
      cursor: not-allowed;

      &:hover {
        color: #fff;
        background-color: $colorGreen;
      }
    }

    &:hover {
      color: $colorGreen;
      background-color: #ededed;
    }
  }

  .footer {
    display: flex;
    justify-content: flex-end;
    margin: 15px 0;

    button {
      border: none;
      margin-left: 10px;
      border-bottom: 1px solid $colorGray;
      background-color: transparent;


    }
  }
}