@import './../../styles/colors.scss';

.custom-header {
  background-color: white;
  // height: 50px;
  text-align: center;
  color: black;
  border-bottom: 1px solid;
  border-color: #8080801c;

  p {
    margin: 15px 0;
    line-height: 22px;
    font-size: 22px;
    padding: 0 10px;

  }
}

.left-header {
  float: left;
}

.header-logo {
  padding: 1% 0;
  width: 150px;
  display: inline-block;
  vertical-align: middle;
}

.company-name {
  text-transform: uppercase;
  color: $colorGreen;
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0;
}