@import './../../styles/colors.scss';

.page-login {
  background-color: $colorBackground;
  height: -webkit-fill-available;

  .logo {
    display: block;
    width: 20%;
    margin: 0 auto;
    padding-top: 40px;
  }

  .form-login {
    margin: 40px 0;

    .form-item {
      display: block;
      text-align: center;
      margin-bottom: 20px;




    }

    button[type=submit] {
      margin: 0 auto;
      color: #fff;
      background-color: $colorGreen;
      display: block;

      width: 150px;
      height: 35px;
      border-radius: 5px;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      -ms-border-radius: 5px;
      -o-border-radius: 5px;
      border: none;
    }
  }
}