.container-text-input {
  position: relative;
  display: inline-block;

  input {
    height: 20px;
    width: 208px;
    border-radius: 5px;
    border: 1px solid #0000002e;
    padding: 0 5px;
    padding: 15px 10px;
  }

  .invalid-input {
    position: absolute;
    bottom: -110%;
    right: 0;
    display: inline;
    color: red;
    font-size: 10px;
    text-align: right;
  }
}